<template>
  <div class="seat"> 
    <img v-if="seat == 7" class="img" src="../assets/5-1.jpg" alt="">
    <img v-if="seat == 8" class="img" src="../assets/5-2.jpg" alt="">
    <img v-if="seat == 9" class="img" src="../assets/5-3.jpg" alt="">
